
import React, {memo, useRef, useState, useEffect} from "react";
import '../../images/FunnyCook/funnyCook.css'
import img_Rightbuttionpush from "../../images/FunnyCook/Rightbuttionpush.png";
import img_Leftbuttionpush from "../../images/FunnyCook/Leftbuttionpush.png";
import img_ButtionA from "../../images/FunnyCook/ButtionA.png";
import img_ButtionB from "../../images/FunnyCook/ButtionB.png";
import {accent, background, gradient, text} from '@sberdevices/plasma-tokens';

import styled  from 'styled-components';
import Ot from "../Ot";





function Controls(props){
    // console.log("render Controls")

    const [Rightbuttionpush, setRightbuttionpush] = useState(false);
    const [Leftbuttionpush, setLeftbuttionpush] = useState(false);

    const [Abuttionpush, setButtionAPush] = useState(false);
    const [Bbuttionpush, setButtionBPush] = useState(false);
    const [Timebuttionpush, setButtionTimePush] = useState(false);

    const callbackR = props.callbackR
    const callbackL = props.callbackL

    const callbackA = props.callGameA
    const callbackB = props.callGameB
    const callbackTime = props.callbackTime

    const demo = props.demo
    const refButtonA = useRef(null)
    const refButtonB = useRef(null)
    const refButtonT = useRef(null)


    // const ButtionA = props.gameA

    // console.log(accent)


    const mouseUpHandlerR = () => {
        if (demo) return;

        setRightbuttionpush( false);
        // console.log("mouseUpHandlerR")
    }
    const mouseDownHandlerR = () => {
        if (demo) return;

        setRightbuttionpush( true);

        callbackR()

    }
    const mouseUpHandlerL = () => {
        if (demo) return;

        setLeftbuttionpush( false);
        // console.log("mouseUpHandlerL")
    }
    const mouseDownHandlerL = () => {
        if (demo) return;

        callbackL()
        setLeftbuttionpush( true);
        // console.log("mouseDownHandlerL")
    }
    const mouseUpHandlerA = () => {
        if (demo) return;

        setButtionAPush( false);

    }
    const mouseDownHandlerA = () => {
        if (demo) return;

        setButtionAPush( true);
        callbackA()
        // console.log("mouseDownHandlerA")
    }
    const mouseUpHandlerB = () => {
        if (demo) return;
        setButtionBPush( false);

    }
    const mouseDownHandlerB = () => {
        if (demo) return;
        setButtionBPush( true);
        callbackB()
        // console.log("mouseDownHandlerB")
    }

    const mouseUpHandlerTime = () => {
        if (demo) return;
        setButtionTimePush( false);

    }
    const mouseDownHandlerTime = () => {
        if (demo) return;
        setButtionTimePush( true);
        callbackTime()
        // console.log("mouseDownHandlerB")
    }




    useEffect(() => {

        if (props.buttonFocus === 0 && refButtonA.current) refButtonA.current.focus()
        if (props.buttonFocus === 1 && refButtonB.current) refButtonB.current.focus()
        if (props.buttonFocus === 2 && refButtonT.current) refButtonT.current.focus()



    }, []);




    // ||
    return (
        <div>

                <div className="Rightbuttionpush"
                     onMouseDown={mouseDownHandlerR}
                     onMouseUp={mouseUpHandlerR}>
                    {(Rightbuttionpush || props.buttonRDown) && <img src={img_Rightbuttionpush} alt={"img_Rightbuttionpush"}/>}
                </div>

                <div className="Leftbuttionpush"
                      onMouseDown={mouseDownHandlerL}
                      onMouseUp={mouseUpHandlerL}>
                    {(Leftbuttionpush || props.buttonLDown) && <img src={img_Leftbuttionpush} alt={"img_Leftbuttionpush"}/>}
                </div>


                <div className="ButtionA"  ref={refButtonA}

                    // onClick={clickGameA}>
                     onMouseDown={mouseDownHandlerA}
                     onMouseUp={mouseUpHandlerA}>

                    { (Abuttionpush || props.buttonADown) && <img src={img_ButtionA} alt={"img_ButtionA"}/>}
                </div>


                <div
                    className="ButtionB" ref={refButtonB}

                     onMouseDown={mouseDownHandlerB}
                     onMouseUp={mouseUpHandlerB} >
                    { (Bbuttionpush || props.buttonBDown)&& <img  src={img_ButtionB} alt={"img_ButtionB"}/>}
                </div>

                <div className="ButtionTime" ref={refButtonT}
                    // onClick={clickGameA}>
                     onMouseDown={mouseDownHandlerTime}
                     onMouseUp={mouseUpHandlerTime}>
                    { (Timebuttionpush || props.buttonTDown) && <img src={img_ButtionA} alt={"img_ButtionA"}/>}
                </div>



        </div>
    )
}
export default memo(Controls);