import React, {memo} from "react";
import '../../images/FunnyCook/funnyCook.css'


import img_MouseThhidden from "../../images/FunnyCook/MouseThhidden.png";
import img_MouseThmeat from "../../images/FunnyCook/MouseThmeat.png";
import img_MouseThsosige from "../../images/FunnyCook/MouseThsosige.png";
import img_MouseThfish from "../../images/FunnyCook/MouseThfish.png";
import img_MouseThchicken from "../../images/FunnyCook/MouseThchicken.png";
import img_Mouse1 from "../../images/FunnyCook/Mouse1.png"

function err(pos){
    const obj = {}

    obj.MouseThhidden = false
    obj.MouseThmeat = false
    obj.MouseThsosige = false
    obj.MouseThfish = false
    obj.MouseThchicken = false
    obj.Mouse1 = false


    switch (pos) {
        case 0:
            obj.MouseThhidden = true;
            break;
        case 1:
            obj.MouseThmeat = true;
            break;
        case 2:
            obj.MouseThsosige = true;
            break;
        case 3:
            obj.MouseThfish = true;
            break;
        case 4:
            obj.MouseThchicken = true;
            break;
        case 5:
            obj.Mouse1 = true;
            break;


        default:
            break;
    }

    return obj;

}

function ErrorFood(props){

    const pos = props.pos

    const obj = err(pos)

    return (
        <>
            {obj.MouseThhidden && <div className="MouseThhidden"><img src={img_MouseThhidden} alt={"err0"} /></div>}
            {obj.MouseThmeat && <div className="MouseThmeat"><img src={img_MouseThmeat} alt="err1"/></div>}
            {obj.MouseThsosige && <div className="MouseThsosige"><img src={img_MouseThsosige} alt="err2"/></div>}
            {obj.MouseThfish && <div className="MouseThfish"><img src={img_MouseThfish} alt="err3"/></div>}
            {obj.MouseThchicken && <div className="MouseThchicken"><img src={img_MouseThchicken} alt="err4"/></div>}
            {obj.Mouse1 && <div className="Mouse1"><img src={img_Mouse1} alt="err5"/></div>}

        </>
    )
};

export default memo(ErrorFood)