import React, {memo, useState} from 'react';
import TweenOne from 'rc-tween-one';
import BezierPlugin from 'rc-tween-one/lib/plugin/BezierPlugin';
import Games from "./Games";

import * as constant from "../const";

import * as data from "../images/FunnyCook/data.js"
import * as helper from "../helpers/helper"

import FunnyCook from "./FunnyCook"
import Ot from "./Ot"

import {detectDevice} from "@sberdevices/ui/utils";
// import * as constant from "../const"
TweenOne.plugins.push(BezierPlugin);


function Main(props) {

    // const [game, setGame] = useState("Ot");

    // const character = props.character

    const width = window.innerWidth  ;
    const height = window.innerHeight ;



    const getscale = () => {

        let device = props.device



        if  ((device === "touch") && (width !== 1920) && false)
        {
            console.log(width, height)
            console.log("kx:", width / 1920.0, width)
            console.log("ky:", height / 1080.0, height)

           if (props.insets.bottom === undefined)
            return {scale :0, rotate : 90, top: 0, left:"100%"}

            let scale = 1080.0 / width

            if (1920.0 * scale > window.innerHeight - props.insets.bottom)
            {
                scale = (window.innerHeight - props.insets.bottom) / 1920.0
            }

            return  {scale :scale, rotate : 90, top: 0, left:"100%"}


            // device = "sberPortal";
        }

        switch (device) {
            case "sberBox":
                return  {scale : height / 1080.0, rotate : 0, top: 0, left:0}
            case "sberPortal":
                return    {scale : height / 1080.0, rotate : 0, top: 0, left:0}

            default:
                return   {scale : height / 1080.0, rotate : 0, top: 0, left:0}
        }

    }
    const param = getscale()




    // const posgame = props.posgame

    const top = 0
    const left = 0

    // if   (props.game !== props.commandfromback)
    // {
    //     switch (props.commandfromback) {
    //
    //         case 'povar': {
    //             setGame('povar')
    //             break;
    //
    //         }
    //
    //     }
    //
    // }

    return (

        <>

            {props.game === 'all' &&  <Games device = {props.device} gameFocus = {props.gameFocus} callbackClickGame = {props.callbackClickGame}/>}



            {props.game === 'povar' && <FunnyCook   scale = {param.scale}
                                                    rotate = {param.rotate}
                                                    top = {param.top}
                                                    left = {param.left}
                                                    // posgame = {posgame}
                                                    // callbackL = {props.callbackL}
                                                    // callbackR = {props.callbackR}
                                                    buttonLDown ={props.buttonLDown}
                                                    buttonRDown ={props.buttonRDown}
                                                    buttonEnterDown = {props.buttonEnterDown}
                                                    commandfromback = {props.commandfromback}
                                                    character = {props.character}
                                                    buttonFocus = {props.buttonFocus}
                                                    callbackEvent = {props.callbackEvent}
                                                    vers = {props.vers}
            /> }

                {props.game === 'ot' && <Ot scale = {param.scale}
                                            rotate = {param.rotate}
                                            top = {param.top}
                                            left = {param.left}
                                            // posgame = {posgame}
                                            // callbackL = {props.callbackL}
                                            // callbackR = {props.callbackR}
                                            buttonLDown ={props.buttonLDown}
                                            buttonRDown ={props.buttonRDown}
                                            commandfromback = {props.commandfromback}
                                            character = {props.character}
                                            buttonEnterDown = {props.buttonEnterDown}
                                            buttonFocus = {props.buttonFocus}
                                            callbackEvent = {props.callbackEvent}
                                            vers = {props.vers}
            /> }

        </>
    );
}

export default memo(Main);

