
import React, {memo} from "react";

import '../../images/FunnyCook/funnyCook.css'

function Score(props){
    // console.log("render Controls")

    function splitToDigits(number) {
        let digits = [];
        while (number) {
            digits.push(number % 10);
            number = Math.floor(number/10);
        }
        return digits;
    }

    const digits = splitToDigits(props.score);

    let style1 =   {position: "absolute", fontFamily: "digital_7", textAlign: "right", top: 300, left:800 + 330, color: "black", zIndex: 99999, fontSize: 100}
    let style2 =   {position: "absolute", fontFamily: "digital_7", textAlign: "right", top: 300, left:750+ 330, color: "black", zIndex: 99999, fontSize: 100}
    let style3 =   {position: "absolute", fontFamily: "digital_7", textAlign: "right", top: 300, left:700+ 330, color: "black", zIndex: 99999, fontSize: 100}

    const d1 = digits.length>0?digits[0].toString():"0"
    const d2 = digits.length>1?digits[1].toString():""
    const d3 = digits.length>2?digits[2].toString():""


    return (
        <>
            <div style={style1}> <div>{d1}</div></div>
            {d2 && <div style={style2}> <div>{d2}</div></div>}
            {d3 && <div style={style3}> <div>{d3}</div></div>}

        </>


    )
};
export default memo(Score);