// clear; for name in *; do printf 'import img_%s \n' "$name from \"../images/FunnyCook/$name"; done


import React ,{useState, memo, useCallback, useRef} from "react";
import produce from "immer"
import BackgroundGame from "./FunnyCook/BackgroundGame"
import Controls from "./FunnyCook/Controls"
import Player from "./FunnyCook/Player"
import Food from "./FunnyCook/Food"
import Vers from "./Vers"
import ErrorFood from "./FunnyCook/ErrorFood";
import Score from "./FunnyCook/Score";
import ErrorCount from "./FunnyCook/ErrorCount";
import Teaport from "./FunnyCook/Teaport";
import Time from "./FunnyCook/Time_"
import Cat from "./FunnyCook/Cat"
import '../images/FunnyCook/funnyCook.css'




import UIfx from 'uifx'

import beep_error_mp3 from '../Sound/sound 3.mp3' // Ð¾ÑˆÐ¸Ð±ÐºÐ°
import beep_move_mp3 from '../Sound/sound 5.mp3' // Ð´Ð²Ð¸Ð¶ÐµÐ½Ð¸Ðµ ÐµÐ´Ñ‹
import beep_score_mp3 from '../Sound/sound 4.mp3'
import beep_click from '../Sound/sound 1.mp3'
import Ot from "./Ot";



const beep_err = new UIfx(beep_error_mp3)
const beep_move = new UIfx(beep_move_mp3)
const beep_score = new UIfx(beep_score_mp3)
const clicksound = new UIfx(beep_click)

//import * as helper from "../helpers/helper";
//import * as data from "../images/FunnyCook/data";





function Funny_cook(props){



        const max = 500

        const [command, setCommand] = useState("none")
        // const commandRef = useRef(command)
        //
        // commandRef.current = command;


        const [demo, setDemo] = useState(true);

        const [heroPos, setHeroPos] = useState(1)

        const countLButton  = useRef(0)
        const countRButton = useRef(0)
        const countEntrButton = useRef(0)


        const buttonLDown = useRef(false)
        const buttonRDown = useRef(false)

        const buttonADown = useRef(false)
        const buttonBDown = useRef(false)
        const buttonTDown = useRef(false)



        const [runner, setRunner] = useState(true)
        const [start, setStart] = useState(true)
        const [meat, setMeat] = useState(1)
        const [sosige, setSosige] = useState(1)
        const [fish, setFish] = useState(1)
        const [chiken, setChiken] = useState(1)

        const [skip, setSkip] = useState(0)
        const [errorfood, setErrorFood] = useState(0)
        const [cat, setCat] = useState(false)

        const [gameAB, setGameAB] = useState('')

        const [hh, setHh] = useState(0)
        const [mm, setMm] = useState(0)
        const [separator, setSeparator] = useState(false)
        const [dp_pp, setDp_pp] = useState(true)


        const [score, setScore] = useState(0)
        const [resetError, setResetError] = useState(false)

        const [teaport, setTeaport] = React.useState(false);

        const [toCat, setToCat] = useState( Math.floor((Math.random() * (10 - 5) + 5)))

        const [errorcount, setErrorcount] = useState(0)

        const [lastPositionCook, setCook] = useState(heroPos)

        const [up, setUp] = useState(0)

        const [currentfood, setCurrentfood] = useState(1)
        const currentfoodgRef = useRef(currentfood)

        const [time, setTime] = React.useState(0);

        const [dt, setDt] = React.useState(0.25);

        const runningRef = useRef(runner)

        const meatRef = useRef(meat)
        const sosigeRef = useRef(sosige)
        const fishRef = useRef(fish)
        const chikenRef = useRef(chiken)

        const scoreRef = useRef(score)

        const errorfoodRef = useRef(errorfood)

        const heroPosRef = useRef(heroPos)

        const lastposCookRef = useRef(lastPositionCook)

        const errorfood_pos_Ref = useRef(0)

        runningRef.current = runner;
        currentfoodgRef.current = currentfood;


        meatRef.current = meat;
        sosigeRef.current = sosige;
        fishRef.current = fish;
        chikenRef.current = chiken;

        // if (!demo){
        //         heroPosRef.current = heroPos
        // }

        const sendEvent = props.callbackEvent;
        function send_event(id, param) {
                sendEvent({ "action_id": id, "parameters" : param } );
        }

        const clickGameA = () => {
                setDt(0.25)
                setScore(0)
                setErrorcount(0)

                setRunner(true)

                currentfoodgRef.current = currentfood;

                setCook(1)
                setDemo(false)

                setGameAB('A');

                restart_game('A', false)

        }
        const clickGameB = () => {
                setDt(0.25)
                setScore(0)
                setErrorcount(0)

                setRunner(true)

                currentfoodgRef.current = currentfood;

                setCook(1)
                setDemo(false)
                setGameAB('B');
                restart_game('B',false)

        }
        const clickGameTime = () => {

                setScore(0)
                setErrorcount(0)

                setRunner(true)

                currentfoodgRef.current = currentfood;

                setCook(1)
                setDemo(true)
                setGameAB('');
                restart_game("", true)

        }
        const error_food = (pos) =>{

                if (!demo) beep_err.play()
                setRunner(false)
                setErrorFood(pos)
                errorfoodRef.current = pos;
                errorfood_pos_Ref.current = pos;
        }
        const changeDt = () =>{


                if (demo){
                        setDt(0.1);
                        return
                }
                let delay = dt;
                let s = score % 1000

                if ((s + 1) % 5 === 0) send_event("score", (s + 1).toString())


                if (s + 1 < 30) delay = 0.25;
                if (s + 1 === 30) delay -= 0.02;
                if (s + 1 === 40) delay -= 0.02;
                if (s + 1 === 60) delay -= 0.02;
                if (s + 1 === 80) delay -= 0.02; //

                if (s + 1 === 200) {
                        delay = 0.25;
                        setRunner(false);
                        setResetError(true);
                        setTimeout(() => {


                                setResetError(false);
                                setRunner(true);
                                setErrorcount(0)

                        }, 4000);
                }
                if (s + 1 === 220) delay -= 0.02;
                if (s + 1 === 240) delay -= 0.02;
                if (s + 1 === 260) delay -= 0.02;


                if (s + 1 === 500) delay = 0.2;
                if (s + 1 === 520) delay -= 0.02;
                if (s + 1 === 540) delay -= 0.02;
                if (s + 1 === 560) delay -= 0.02;


                setDt(delay)
        }
        const restart_game = (gameB = "", demo) => {


                setMeat(1)
                setSosige( 1)
                setFish( 1)

                if ((gameB === "B") ||  ((demo) && (gameB === ""))){
                        setChiken(1)
                }else{
                        setChiken(0)
                }


                setStart(true)

        }
        const clear_game = (gameB = false) => {
                setMeat(0)
                setSosige( 0)
                setFish( 0)
                if (gameB === true) {
                        setChiken(0)
                }




        }
        const nextFood = (food, pos3 = false) => {

                const pos = heroPosRef.current



                if (food === skip)
                {
                        setSkip(0)
                        if (!demo) beep_move.play();
                        return
                }

                if (food === 1) {


                        setMeat((prev) => {

                                if  (cat && ((prev === 3) || (prev === 7))) return 7;

                                if (prev === 0) {return 0;}
                                if (pos3) { return 3; }
                                let s = 0;

                                if (pos === food) s = 2;

                                if (prev !== 9){
                                        if (!demo) beep_move.play();
                                }


                                const x = prev >= 9 ? s : prev + 1;
                                meatRef.current = x;
                                return x;



                        })

                        if ((pos === food)) {
                                if ((meatRef.current === 9) ) {

                                        setTimeout(() => {
                                                setUp(food)
                                                nextFood(food)
                                                if (!demo) beep_score.play()
                                                setScore(prevState => prevState + 1)
                                                changeDt()
                                        }, 200);
                                        setTimeout(() => {
                                                setUp(0)

                                        }, 400);
                                }

                        }

                        if (meatRef.current === 0) {error_food(food)}



                }

                if (food === 2){
                        setSosige( (prev) => {
                                if (prev === 0){ return 0}
                                if (pos3) {
                                        return 3;
                                }
                                let s = 0;
                                if (pos === food) s = 2;
                                const x = prev >= 9 ? s: prev + 1 ;
                                if (prev !== 9){
                                        if (!demo) beep_move.play();
                                }
                                sosigeRef.current = x;

                                return x;

                        })
                        if ((pos === food)) {
                                if ((sosigeRef.current === 9) ) {
                                        setTimeout(() => {
                                                setUp(food)
                                                nextFood(food)
                                                if (!demo) beep_score.play()
                                                setScore(prevState => prevState + 1)
                                                changeDt()
                                        }, 200);
                                        setTimeout(() => {
                                                setUp(0)

                                        }, 400);


                                }
                        }
                        if (sosigeRef.current === 0) {error_food(food)}
                }

                if (food === 3){
                        setFish( (prev) => {
                                if (prev === 0){ return 0}
                                if (pos3) {
                                        return 3;
                                }
                                let s = 0;
                                if (pos === food) s = 2;
                                if (prev !== 9){
                                        if (!demo) beep_move.play();
                                }
                                const x = prev >= 9 ? s : prev + 1;

                                fishRef.current = x;


                                return x;

                        })
                        if ((pos === food)) {
                                if ((fishRef.current === 9) ) {
                                        setTimeout(() => {
                                                setUp(food)
                                                setScore(prevState => prevState + 1)
                                                if (!demo) beep_score.play()
                                                changeDt()
                                                nextFood(food)
                                        }, 200);
                                        setTimeout(() => {
                                                setUp(0)

                                        }, 400);
                                }
                        }
                        if (fishRef.current === 0) {error_food(food)}
                }

                if (food === 4){
                        setChiken( (prev) => {
                                if (prev === 0){ return 0}
                                if (pos3) {
                                        return 3;
                                }
                                let s = 0;
                                if (pos === food) s = 2;
                                if (prev !== 9){
                                        if (!demo) beep_move.play();
                                }
                                const x = prev >= 9 ? s : prev + 1;

                                chikenRef.current = x;


                                return x;
                        })
                        if ((chikenRef.current === 9) ) {
                                setTimeout(() => {
                                        setUp(food)
                                        setScore(prevState => prevState + 1)
                                        if (!demo) beep_score.play()
                                        changeDt()
                                        nextFood(food)
                                }, 200);
                                setTimeout(() => {
                                        setUp(0)

                                }, 400);
                        }
                        if (chikenRef.current === 0) {error_food(food)}
                }



        }
        const moveDemo = () => {

                const arr = new Array( meatRef.current, sosigeRef.current, fishRef.current, chikenRef.current)
                let max = arr[heroPosRef.current-1];
                let index = heroPosRef.current-1;
                for (let i = 0; i < 4; i++) {
                        if  (max< arr[i]){
                                index = i;
                                max = arr[i];

                        }
                }


                if (max>6){
                        if (heroPosRef.current < index+1){

                                heroPosRef.current += 1;
                        }
                        if (heroPosRef.current > index+1){

                                heroPosRef.current -= 1;
                        }
                }



        }



        const callbackL = React.useCallback(() => {

                clicksound.play()
                if (demo) return;
                setHeroPos( (prev) => {
                        const x = prev>1?-1:0;
                        heroPosRef.current = prev + x;
                        return prev + x;
                })
        }, [demo])

        const callbackR = React.useCallback(() => {
                clicksound.play()
                if (demo) return;
                setHeroPos( (prev) => {
                        const max = 4;
                        const x = prev<max?1:0;
                        heroPosRef.current = prev + x;
                        return prev + x;

                })
        }, [demo])
        const checkButtonDown = (l, r, entr) => {

                // console.log(l,r,entr, countLButton.current, countRButton.current, countEntrButton.current)

                if  ((countEntrButton.current !== entr) && (entr === 0)){
                        console.log("ÑÐ±Ñ€Ð¾ÑÐ¸Ð» entr", entr, countEntrButton.current)
                                buttonADown.current = false;
                                buttonBDown.current = false;
                                buttonTDown.current = false;

                                countEntrButton.current = 0

                                if (props.buttonFocus === 0){

                                        clickGameA()

                                }
                                if (props.buttonFocus === 1){

                                        clickGameB()

                                }
                                if (props.buttonFocus === 2){

                                        clickGameTime()


                                }



                }

                if  ((countLButton.current !== l) && (l === 0)){
                        buttonLDown.current = false
                        countLButton.current = 0
                }
                if  ((countRButton.current !== r) && (r === 0)){
                        buttonRDown.current = false
                        countRButton.current = 0
                }

                if ((entr > 0) && (entr !== countEntrButton.current)){
                        countEntrButton.current = entr

                        if (props.buttonFocus === 0){
                                buttonADown.current = true;
                                buttonBDown.current = false;
                                buttonTDown.current = false;


                        }
                        if (props.buttonFocus === 1){
                                buttonADown.current = false;
                                buttonBDown.current = true;
                                buttonTDown.current = false;


                        }
                        if (props.buttonFocus === 2){
                                buttonADown.current = false;
                                buttonBDown.current = false;
                                buttonTDown.current = true;



                        }


                }
                // else{
                //         buttonADown.current = false;
                //         buttonBDown.current = false;
                //         buttonTDown.current = false;
                // }

                if ((l > 0) && (l !== countLButton.current)) {

                        countLButton.current = l
                        buttonLDown.current = true

                        if (demo) return;
                        setHeroPos((prev) => {
                                const x = prev > 1 ? -1 : 0;
                                heroPosRef.current = prev + x;
                                return prev + x;
                        })


                }
                //  }else
                // {
                //         buttonLDown.current = false
                // }

                if ((r>0) && (r !== countRButton.current)) {

                        countRButton.current = r
                        buttonRDown.current = true
                        if (demo) return;
                        setHeroPos( (prev) => {
                                const max = 4;
                                const x = prev<max?1:0;
                                heroPosRef.current = prev + x;
                                return prev + x;

                        })
                }
                // else
                // {
                //         buttonRDown.current = false
                // }


        }

        React.useEffect(() => {
                const timer = window.setInterval(() => {

                        setTime(prevTime => prevTime + 1); // <-- Change this line!

                        if (props.buttonLDown !== countLButton.current ||
                            props.buttonRDown !== countRButton.current ||
                            props.buttonEnterDown !== countEntrButton.current)  checkButtonDown(props.buttonLDown, props.buttonRDown, props.buttonEnterDown)

                        if (errorfoodRef.current > 0) {

                                if (time % 30 === 0) {
                                        if (!demo) beep_err.play()
                                        setErrorFood((prev) => {

                                                const x = prev >= 5 ? 0 : prev + 1;
                                                errorfoodRef.current = x;
                                                return x;

                                        })
                                        if (errorcount<=5 || demo)
                                        {

                                                if (errorfoodRef.current === 0){
                                                        if (!demo) beep_err.play()
                                                        if (errorcount<5 || demo){
                                                                setRunner(true)
                                                                setErrorcount((prevState) => {

                                                                        const add = teaport===true?1:2;
                                                                        return prevState + add;
                                                                        }
                                                                    )
                                                                restart_game(gameAB, demo)
                                                        }
                                                        if (errorcount >= 5 && !demo)  {
                                                                setErrorcount((prevState) => {

                                                                            const add = teaport===true?1:2;
                                                                            return prevState + add;
                                                                    }
                                                                )
                                                                // setErrorcount(prevState => prevState + 1)


                                                                        setErrorFood(-1);
                                                                        clear_game();
                                                        }
                                                }
                                        }

                                }
                        }

                        if (!runner){
                                return
                        }


                         if (start){
                                setTimeout(() => {

                                        setStart(false)
                                }, 2000);
                         return;
                         }


                        // if ((props.posgame === 1) && (props.posgame === up) && (meatRef.current > 2) ) {
                        //         setUp(0)
                        // }



                        getUp();


                        const t = Math.floor(dt * 30 / 0.25);

                        if (time % (t) === 0) {

                                if (start){
                                        setStart(false)
                                }



                                if (toCat === 1){
                                        setToCat(Math.floor((Math.random() * (30 - 10) + 10)))
                                        setCat(prevState => !prevState)
                                }
                                else{
                                        setToCat(prevState => prevState - 1)
                                }


                                // if ((Math.random()<0.03)){
                                //         setCat(prevState => !prevState)
                                //         const t =  Math.random() * (6000 - 2000) + 2000;
                                //         console.log("Ð¶Ð´Ñƒ", t)
                                //
                                //         setTimeout(() => {
                                //                 setTeaport(false)
                                //         }, t);
                                //
                                //
                                // }

                                if ((!teaport) && (Math.random()<0.03)){
                                        setTeaport(true)

                                        const t =  Math.random() * (6000 - 2000) + 2000;
                                        setTimeout(() => {
                                                setTeaport(false)
                                        }, t);


                                }

                                setCurrentfood((prev) => {
                                        let stop = 3;
                                        if ((gameAB === 'B') || (gameAB === '')) stop =4;

                                        const x = prev >= stop ? 1 : prev + 1;
                                        return x;

                                })

                                nextFood(currentfood)
                                // checkBeginError(props.posgame)

                                // checkEndError(props.posgame)



                        }

                        if (demo &&  Math.floor(time % 15) === 0)
                        {
                                moveDemo()
                        }

                }, 10);
                return () => {
                        window.clearInterval(timer);
                };
        }, [time]);
        React.useEffect(() => {
                const timer = window.setInterval(() => {

                        setSeparator(prevState => !prevState)
                        const d = new Date();
                        //                         let unix_timestamp = 1549312452
                        // // Create a new JavaScript Date object based on the timestamp
                        // // multiplied by 1000 so that the argument is in milliseconds, not seconds.
                        //                         var date = new Date(unix_timestamp * 1000);
                        // // Hours part from the timestamp
                        //                         var hours = date.getHours();
                        // // Minutes part from the timestamp
                        //                         var minutes = "0" + date.getMinutes();
                        // // Seconds part from the timestamp
                        //                         var seconds = "0" + date.getSeconds();
                        //
                        // // Will display time in 10:30:23 format
                        //                         var formattedTime = hours + ':' + minutes.substr(-2) + ':' + seconds.substr(-2);

                        // console.log(formattedTime);
                        setDp_pp(d.getHours() < 12);
                        setHh((d.getHours() < 12) ? d.getHours() : d.getHours() - 12);
                        setMm(d.getMinutes());



                }, 1000);
                return () => {
                        window.clearInterval(timer);
                };
        }, [separator]);


        function upfood(pos, food, stand = false){

                if ((food === 9)){

                        setTimeout(() => {

                                setScore(prevState => prevState + 1)
                                if (!demo) beep_score.play()
                                changeDt()

                                nextFood(heroPosRef.current)
                                setSkip(pos)
                                setUp(heroPosRef.current)
                        }, 200);
                        setTimeout(() => {
                                setUp(0)

                        }, 500);

                        const addtime = time % 50
                        setTime(prevTime => prevTime + addtime)
                        setCurrentfood((prev) => {
                                const x = prev >= 3 ? 1 : prev + 1;
                                return x;
                        })
                }

                if ((food === 8)){

                        setUp(heroPosRef.current)
                        setSkip(pos)
                        if (!demo) beep_move.play();
                        setScore(prevState => prevState + 1)
                        if (!demo) beep_score.play()
                        changeDt()
                        setTimeout(() => {
                                 nextFood(heroPosRef.current, true)

                        }, 200);

                        const addtime = time % 50
                        setTime(prevTime => prevTime + addtime)
                        setCurrentfood((prev) => {
                                const  x = prev >= 3 ? 1 : prev + 1;
                                return x;
                        })
                        setTimeout(() => {
                                setUp(0)
                        }, 500);
                }
        }
        function ckeck(pos) {


                if (  ((pos === 1) && (meatRef.current   > 3)  && (up === 1)) ||
                      ((pos === 2) && (sosigeRef.current > 3)  && (up === 2)) ||
                      ((pos === 3) && (fishRef.current   > 3)  && (up === 3)) ||
                      ((pos === 4) && (chikenRef.current > 3)  && (up === 4))) {
                              setUp(0)
                        }

                if (pos === 1) {
                        upfood(pos, meatRef.current)
                }
                if (pos === 2) {
                        upfood(pos, sosigeRef.current)
                }
                if (pos === 3) {
                        upfood(pos, fishRef.current)
                }
                if (pos === 4) {
                        upfood(pos, chikenRef.current)
                }

        }

        function getUp() {

                if (!demo && lastposCookRef.current !== heroPos) {

                        lastposCookRef.current = heroPos
                        setCook(lastposCookRef.current)
                        ckeck(lastposCookRef.current)

                }
                if (demo && lastposCookRef.current !== heroPosRef.current) {

                        lastposCookRef.current = heroPosRef.current
                        ckeck(lastposCookRef.current)

                }
        }

        if ((props.commandfromback === 'povar_gameA') && (command !== props.commandfromback)) {

                setCommand(props.commandfromback)
                clickGameA()
        }

        if ((props.commandfromback === 'povar_gameB') && (command !== props.commandfromback)) {

                setCommand(props.commandfromback)
                clickGameB()
        }

        const styles = {
                transform: `scale( ${props.scale}, ${props.scale})`,
                position: "absolute",
                top: `${props.top}px`,
                left: `${props.left}px`

        };


        return (

            <div style={styles}>
                   <div style = {{transform: `rotate( ${props.rotate}deg )`}}>
                           <BackgroundGame />

                           {props.scale > 0.3 && <Controls
                                     callbackL = {callbackL}
                                     callbackR = {callbackR}
                                     buttonLDown = { buttonLDown.current}
                                     buttonRDown = { buttonRDown.current}

                                     buttonADown = { buttonADown.current}
                                     buttonBDown = { buttonBDown.current}
                                     buttonTDown = { buttonTDown.current}

                                     callGameA = {clickGameA}
                                     callGameB = {clickGameB}
                                     callbackTime = {clickGameTime}
                                     demo = {props.demo}
                                     character = {props.character}

                                     buttonFocus = {props.buttonFocus}

                           />}
                           <Player posgame = {heroPosRef.current} up = {up} start = {start} gameAB = {gameAB}/>
                           <Food meat = {meat} sosige = {sosige} fish = {fish} chiken = {chiken}/>
                           <ErrorFood pos = {errorfood} />
                            {!demo && <Score score = {score % 1000} />}
                            {!demo && <ErrorCount count = {errorcount} resetError = {resetError}/>}
                            {!demo && <ErrorCount count = {errorcount} resetError = {resetError}/>}
                           <Cat catStay = {!cat} catFork = {cat} />
                           <Teaport teaport = {teaport} />

                            {demo && <Time hh = {hh} mm = {mm} dp_pp = {dp_pp} sep = {separator}/>}


                    </div>
            </div>);
};

export default memo(Funny_cook);

