
import React ,{ memo} from "react";
// import '../../images/OT/styles.css'

import img_Driver1 from "../../images/OT/Diver1.png";
import img_Driver2 from "../../images/OT/Diver2.png";
import img_Driver3 from "../../images/OT/Diver3.png";
import img_Driver4 from "../../images/OT/Diver4.png";

import img_DriverOnBoard1 from "../../images/OT/Diverboat1.png";
import img_DriverOnBoard2 from "../../images/OT/Diverboat2.png";
import img_DriverOnBoard3 from "../../images/OT/Diverboat3.png";


import img_DriverOnBoard1h1 from "../../images/OT/Diverboat1hand1.png";
import img_DriverOnBoard1h2 from "../../images/OT/Diverboat1hand2.png";

import img_Driver5 from "../../images/OT/Diver5.png";
import img_Driver5h1 from "../../images/OT/Diver5hand1.png";
import img_Driver5h2 from "../../images/OT/Diver5hand2.png";
import img_Driver5h3 from "../../images/OT/Diver5hand3.png";

import img_Treasure1 from "../../images/OT/Treasure1.png";
import img_Treasure2 from "../../images/OT/Treasure2.png";
import img_Treasure3 from "../../images/OT/Treasure3.png";
import img_Treasure4 from "../../images/OT/Treasure4.png";
import img_Treasure5 from "../../images/OT/Treasure5.png";


import img_Deaddiverhand1 from "../../images/OT/Deaddiverhand1.png";
import img_Deaddiverhand2 from "../../images/OT/Deaddiverhand2.png";
import img_Deaddiverhand3 from "../../images/OT/Deaddiverhand3.png";
import img_Deaddiverhead from "../../images/OT/Deaddiverhead.png"
import img_Deaddiverleg1 from "../../images/OT/Deaddiverleg1.png";
import img_Deaddiverleg2 from "../../images/OT/Deaddiverleg2.png";
import img_Deaddiverleg3 from "../../images/OT/Deaddiverleg3.png";
import img_Deaddiverleg4 from "../../images/OT/Deaddiverleg4.png";


import img_gamea from "../../images/OT/Gamea.png";
import img_gameb from "../../images/OT/Gameb.png";

function Driver(driver,  start, bag, hand, boardTreasure, live, deathAnim) {





    if (start){
        const obj = {}

        obj.Deaddiverhand1 = true
        obj.Deaddiverhand2 = true
        obj.Deaddiverhand3 = true

        obj.Deaddiverleg1 = true
        obj.Deaddiverleg2 = true
        obj.Deaddiverleg3 = true
        obj.Deaddiverleg4 = true

        obj.Deaddiverhead = true

        obj.Driver1 = true
        obj.Driver5_h1 = true
        obj.Driver5_h2 = true
        obj.Driver5_h3 = true

        obj.Driver2 = true
        obj.Driver3 = true
        obj.Driver4 = true
        obj.Driver5 = true

        obj.DriverOnBoard1 = true
        obj.DriverOnBoard1h1 = true
        obj.DriverOnBoard1h2 = true

        obj.DriverOnBoard2 = true
        obj.DriverOnBoard3 = true

        obj.Treasure1 = true
        obj.Treasure2 = true
        obj.Treasure3 = true
        obj.Treasure4 = true
        obj.Treasure5 = true




        return obj
    }

    const obj = {}

    obj.Driver1 = false
    obj.Driver2 = false
    obj.Driver3 = false
    obj.Driver4 = false
    obj.Driver5 = false

    obj.DriverOnBoard1 = false
    obj.DriverOnBoard1h1 = false
    obj.DriverOnBoard1h2 = false

    obj.DriverOnBoard2 = false
    obj.DriverOnBoard3 = false

    obj.Driver5_h1 = false
    obj.Driver5_h2 = false
    obj.Driver5_h3 = false

    obj.Treasure1 = false
    obj.Treasure2 = false
    obj.Treasure3 = false
    obj.Treasure4 = false
    obj.Treasure5 = false

    obj.Deaddiverhead = false

    obj.Deaddiverhand1 = false
    obj.Deaddiverhand2 = false
    obj.Deaddiverhand3 = false

    obj.Deaddiverleg1 = false
    obj.Deaddiverleg2 = false
    obj.Deaddiverleg3 = false
    obj.Deaddiverleg4 = false

    switch (live){

        case 1:
            obj.DriverOnBoard1 = true
            break;
        case 2:
            obj.DriverOnBoard1 = true
            obj.DriverOnBoard2 = true
            break;

        case 3:
            obj.DriverOnBoard1 = true
            obj.DriverOnBoard2 = true
            obj.DriverOnBoard3 = true
            break;
    }
    if (driver>0)
        obj.DriverOnBoard1 = false

    if (deathAnim >0) {

        obj.Deaddiverhead = true
        obj.Deaddiverhand1 = true

        if (deathAnim === 1){

            obj.Deaddiverhand2 = true
            obj.Deaddiverleg2 = true
            obj.Deaddiverleg3 = true

        }
        if (deathAnim === 2){

            obj.Deaddiverhand3 = true
            obj.Deaddiverleg4 = true
            obj.Deaddiverleg1 = true

        }



        return obj;
    }

    if (driver === 5)
    switch (hand) {
        case 0: obj.Driver5_h2 = true;
            break;
        case 1: obj.Driver5_h1 = true;
            break;
        case 2: obj.Driver5_h2 = true;
            break;
        case 3: obj.Driver5_h3 = true;
            break;


        default:
            break;
    }


    
    if (bag>0)
        switch (driver) {


            case 1: obj.Treasure1 = true;
                break;
            case 2: obj.Treasure2 = true;
                break;
            case 3: obj.Treasure3 = true;
                break;
            case 4: obj.Treasure4 = true;
                break;
            case 5: obj.Treasure5 = true;
                break;

            default:
                break;
        }

    if (driver === 0){
        switch (boardTreasure){
            case 0:
            case 6: obj.DriverOnBoard1h2 = true;
                break;
            case 1:
            case 2:
            case 3:
            case 4:
            case 5:
             obj.DriverOnBoard1h1 = true;
                break;
        }
    }
    switch (driver) {
            case 0: obj.DriverOnBoard1 = true;
                break;
            case 1: obj.Driver1 = true;
                break;
            case 2: obj.Driver2 = true;
                break;
            case 3: obj.Driver3 = true;
                break;
            case 4: obj.Driver4 = true;
                break;
            case 5: obj.Driver5 = true;
                break;

            default:
                break;
    }


    return obj;

}

function Player(props){

    // console.log("render Player")
    const obj = Driver(props.posgame, props.start, props.bag, props.hand, props.treasureBoard, props.live, props.deathAnim)




    return (
        <>
            {obj.Driver1 && <div className="OT_Diver1"><img src={img_Driver1} alt={"img_Driver1"}/></div>}
            {obj.Driver2 && <div className="OT_Diver2"><img src={img_Driver2} alt={"img_Driver2"}/></div>}
            {obj.Driver3 && <div className="OT_Diver3"><img src={img_Driver3} alt={"img_Driver3"}/></div>}
            {obj.Driver4 && <div className="OT_Diver4"><img src={img_Driver4} alt={"img_Driver4"}/></div>}
            {obj.Driver5 && <div className="OT_Diver5"><img src={img_Driver5} alt={"img_Driver5"}/></div>}

            {obj.Treasure1 && <div className="OT_Treasure1"><img src={img_Treasure1} alt={"img_Treasure1"}/></div>}
            {obj.Treasure2 && <div className="OT_Treasure2"><img src={img_Treasure2} alt={"img_Treasure2"}/></div>}
            {obj.Treasure3 && <div className="OT_Treasure3"><img src={img_Treasure3} alt={"img_Treasure3"}/></div>}
            {obj.Treasure4 && <div className="OT_Treasure4"><img src={img_Treasure4} alt={"img_Treasure4"}/></div>}
            {obj.Treasure5 && <div className="OT_Treasure5"><img src={img_Treasure5} alt={"img_Treasure5"}/></div>}


            {obj.Driver5_h1 && <div className="OT_Diver5hand1"><img src={img_Driver5h1} alt={"img_Driver5h1"}/></div>}
            {obj.Driver5_h2 && <div className="OT_Diver5hand2"><img src={img_Driver5h2} alt={"img_Driver5h2"}/></div>}
            {obj.Driver5_h3 && <div className="OT_Diver5hand3"><img src={img_Driver5h3} alt={"img_Driver5h3"}/></div>}


            {obj.DriverOnBoard1 && <div className="OT_Diverboat1"><img src={img_DriverOnBoard1} alt={"img_DriverOnBoard1"}/></div>}
            {obj.DriverOnBoard1h1 && <div className="OT_Diverboat1hand1"><img src={img_DriverOnBoard1h1} alt={"img_DriverOnBoard1h1"}/></div>}
            {obj.DriverOnBoard1h2 && <div className="OT_Diverboat1hand2"><img src={img_DriverOnBoard1h2} alt={"img_DriverOnBoard1h2"}/></div>}


            {obj.DriverOnBoard2 && <div className="OT_Diverboat2"><img src={img_DriverOnBoard2} alt={"img_DriverOnBoard2"}/></div>}
            {obj.DriverOnBoard3 && <div className="OT_Diverboat3"><img src={img_DriverOnBoard3} alt={"img_DriverOnBoard3"}/></div>}

            {obj.Deaddiverhand1 && <div className="OT_Deaddiverhand1"><img src={img_Deaddiverhand1} alt={"img_Deaddiverhand1"}/></div>}
            {obj.Deaddiverhand2 && <div className="OT_Deaddiverhand2"><img src={img_Deaddiverhand2} alt={"img_Deaddiverhand2"}/></div>}
            {obj.Deaddiverhand3 && <div className="OT_Deaddiverhand3"><img src={img_Deaddiverhand3} alt={"img_Deaddiverhand3"}/></div>}

            {obj.Deaddiverleg1 && <div className="OT_Deaddiverleg1"><img src={img_Deaddiverleg1} alt={"img_Deaddiverleg1"}/></div>}
            {obj.Deaddiverleg2 && <div className="OT_Deaddiverleg2"><img src={img_Deaddiverleg2} alt={"img_Deaddiverleg2"}/></div>}
            {obj.Deaddiverleg3 && <div className="OT_Deaddiverleg3"><img src={img_Deaddiverleg3} alt={"img_Deaddiverleg3"}/></div>}
            {obj.Deaddiverleg4 && <div className="OT_Deaddiverleg4"><img src={img_Deaddiverleg4} alt={"img_Deaddiverleg4"}/></div>}

            {obj.Deaddiverhead && <div className="OT_Deaddiverhead"><img src={img_Deaddiverhead} alt={"img_Deaddiverhead"}/></div>}




            {props.gameAB === 'A' && <div className="OT_Gamea"><img src={img_gamea} alt={"gamea"} /></div>}
            {props.gameAB === 'B' && <div className="OT_Gameb"><img src={img_gameb} alt={"gameb"} /></div>}
        </>
    )
}
export default memo(Player);