        const scoreRef = useRef(score)
        const heroPosRef = useRef(heroPos)
        const treasureRef = useRef(treasure)
        const treasureBoardRef = useRef(treasureBoard)
        const addScoreRef = useRef(addScore)
        const dtRef = useRef(dt)

        dtRef.current = dt
        const timeVlodkeRef = useRef(timeVlodke)
