
import React, {memo} from "react";

import '../../images/OT/styles.css'
import img_pp from "../../images/OT/pp.png";
import img_dp from "../../images/OT/dp.png";


function TimeGameAB(props){


    function splitToDigits(number) {
        let digits = [];
        while (number) {
            digits.push(number % 10);
            number = Math.floor(number/10);
        }
        return digits;
    }

    const digits_HH = splitToDigits(props.hh);
    const digits_MM = splitToDigits(props.mm);


    let style_h1 =   {position: "absolute", fontFamily: "digital_7", textAlign: "right", top: 320-45, left:640+350, color: "black", zIndex: 99999, fontSize: 100}
    let style_h2 =   {position: "absolute", fontFamily: "digital_7", textAlign: "right", top: 320-45, left:690+350, color: "black", zIndex: 99999, fontSize: 100}

    let style_m1 =   {position: "absolute", fontFamily: "digital_7", textAlign: "right", top: 320-45, left:750+350, color: "black", zIndex: 99999, fontSize: 100}
    let style_m2 =   {position: "absolute", fontFamily: "digital_7", textAlign: "right", top: 320-45, left:800+350, color: "black", zIndex: 99999, fontSize: 100}


    let style5 =   {position: "absolute", fontFamily: "digital_7", textAlign: "right", top: 320-45, left:720+350, color: "black", zIndex: 99999, fontSize: 100}

    const h1 = digits_HH.length>1?digits_HH[1].toString():"0"
    const h2 = digits_HH.length>0?digits_HH[0].toString():"0"

    const m1 = digits_MM.length>1?digits_MM[1].toString():"0"
    const m2 = digits_MM.length>0?digits_MM[0].toString():"0"


    return (
        <>
            <div style={style_m1}> <div>{m1}</div></div>
            <div style={style_m2}> <div>{m2}</div></div>

            <div style={style_h1}> <div>{h1}</div></div>
            <div style={style_h2}> <div>{h2}</div></div>

            {props.sep && <div style={style5}> <div>:</div></div>}

            {props.dp_pp && <div className="OT_dp"><img src={img_dp} alt={"1dp"} /></div>}
            {!props.dp_pp && <div className="OT_pp"><img src={img_pp} alt={"1pp"} /></div>}



        </>


    )
}

export default memo(TimeGameAB);