import React, {memo, useState} from "react";
import img_Mouse from "../../images/FunnyCook/Mouse2.png";





function errorobj(c){
    const obj = {}

    obj.Mouse1 = false
    obj.Mouse2 = false
    obj.Mouse3 = false

    obj.blink1 = false
    obj.blink2 = false
    obj.blink3 = false

    switch (c) {

        case 0:

            break;

        case 1:
            obj.Mouse1 = true;

            obj.blink1 = true;

            break;
        case 2:
            obj.Mouse1 = true;


            break;
        case 3:
            obj.Mouse1 = true;
            obj.Mouse2 = true;

            obj.blink2 = true;
            break;

        case 4:
            obj.Mouse1 = true;
            obj.Mouse2 = true;


            break;
        case 5:
            obj.Mouse1 = true;
            obj.Mouse2 = true;
            obj.Mouse3 = true;

            obj.blink3 = true;
            break;
        case 6:
            obj.Mouse1 = true;
            obj.Mouse2 = true;
            obj.Mouse3 = true;


            break;

        default:
            obj.Mouse1 = true;
            obj.Mouse2 = true;
            obj.Mouse3 = true;

            break;
    }

    return obj;

}

function ErrorCount(props){
    // console.log("render Controls")



    const [blink, setblink] = useState(true)
    const [blink2, setblink2] = useState(true)
    const [time, setTime] = React.useState(0);

    const obj = errorobj(props.count)

    const resetError = props.resetError
    function getshow(blink, obj_blink, ){

        let show;

        if (obj_blink){
         if (blink) {
                show = true;
            }else {
                show = false;
            }
        }
        else{
            show = true;
        }


        return show
    }
    React.useEffect(() => {
    const timer = window.setInterval(() => {

        setTime(prevTime => prevTime + 1); // <-- Change this line!


        if (time % 2){
            setblink(prevState => !prevState);
            // console.log("blink", blink)
        }
        if (time ){
            setblink2(prevState => !prevState);

        }
        }, 500);
        return () => {
            window.clearInterval(timer);
        };
    }, [time]);



    // React.useEffect(() => {
    //
    //         const timer = setTimeout(() => {
    //             setblink(prevState => !prevState);
    //         }, 1000);
    //         return () => clearTimeout(timer);
    //     }
    // );
    //
    // React.useEffect(() => {
    //
    //        const timer2 = setTimeout(() => {
    //                 setblink2(prevState => !prevState);
    //             }, 500);
    //             return () => clearTimeout(timer2);
    //          }
    // );



    let show1 = getshow(blink, obj.blink1)
    let show2 = getshow(blink, obj.blink2)
    let show3 = getshow(blink, obj.blink3)

    if (resetError){

        console.log(blink2)

        show1 = blink2;
        show2 = blink2;
        show3 = blink2;

    }

    return (
            <>
                {obj.Mouse1 && show1 && <div className="Mouse4"> <img src={img_Mouse}  alt="m1"/></div>}
                {obj.Mouse2 && show2 && <div className="Mouse3"> <img src={img_Mouse}  alt="m1"/></div>}
                {obj.Mouse3 && show3 && <div className="Mouse2"> <img src={img_Mouse}  alt="m1"/></div>}
            </>

    )
}
export default memo(ErrorCount);